.services-security {
    margin-top: 130px;

    &__container {
        display: flex;
    }

    &__left {
        margin-right: 95px;
    }

    &__title {
        margin: 0;
        font-size: 32px;
        line-height: 37px;
        color: $black;
        margin-bottom: 10px;
    }

    &__subtitle {
        margin: 0;
        font-size: 24px;
        line-height: 28px;
        color: $gray;
        margin-bottom: 65px;
    }
}

.services {}

.services__items {
    color: $black;
    margin-bottom: 30px;
    width: 600px;
}

.services__item {
    margin-bottom: 30px;
    width: 100%;
}

.services__item-title {
    width: 100%;
    text-align: left;
    font-size: 18px;
    border: none;
    background-color: transparent;
    color: $blue;
    position: relative;
    font-size: 24px;
    line-height: 28px;
    font-weight: 700;
    border-bottom: 1px solid $gray;
}

.services__items._init .services__item-title {
    padding-bottom: 25px;
    cursor: pointer;
}

.services__items._init .services__item-title::before,
.services__items._init .services__item-title::after {
    content: '';
    width: 18px;
    height: 2px;
    background-color: $black;
    position: absolute;
    right: 0;
    top: 20px;
    transition: transform 0.3s ease 0s;
}

.services__items._init .services__item-title::after {
    transform: rotate(-90deg);
}

.services__items._init .services__item-title._active::after {
    transform: rotate(0deg);
}

.services__item-content {
    margin-top: 32px;
}


.services-item-content {
    &__wrapper {
        display: flex;
        justify-content: space-between;
    }

    &__img {
        width: 160px;
        height: 160px;
        flex-shrink: 0;
        background-color: $blue;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
        }
    }

    &__descr {
        margin-left: 20px;
    }

    &__text {
        font-size: 16px;
        line-height: 29px;
        color: $black;
    }

    &__link {
        text-transform: uppercase;
        font-weight: 700;
        font-size: 12px;
        line-height: 20px;
        padding: 5px 15px 5px 0px;
        color: $gray;
        margin-top: 5px;
        transition: color 0.3s ease-in-out;

        &:hover {
            color: darken($gray, 20%);
        }
    }
}


.security {
    &__img {
        max-width: 474px;
        max-height: 323px;
        background-color: $blue;

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__descr {
        margin-top: 30px;
        font-size: 16px;
        line-height: 29px;
        color: $black;
    }

    &__btn {
        margin-top: 20px;
        padding: 6px 20px;
        font-size: 12px;
        line-height: 23px;

    }
}
