.hero {
    .swiper-button-prev,
    .swiper-button-next {
        
        &::after {
            content: '';
            background-image: url('../img/arrow-right.svg');
            width: 19px;
            height: 35px;
            top: 0;
            left: 0;
        }
    }

    .swiper-button-prev {
        left: 100px;
        &::after{
            transform: rotate(-180deg);
        }
    }

    .swiper-button-next {
        right: 100px;
    }

}

.hero-slide {
    background-repeat: no-repeat;
    background-size: cover;
    background-color: $gray;
    &__container {
        display: flex;
        justify-content: flex-end;
    }

    &__inner {
        padding-top: 150px;
        padding-bottom: 135px;
        max-width: 585px;
    }

    &__subtitle {
        font-size: 28px;
        line-height: 32px;
    }

    &__title {
        margin: 0;
        margin-top: 2px;
        font-weight: bold;
        font-size: 84px;
        line-height: 96px;
    }

    &__descr {
        margin-top: 2px;
        font-size: 32px;
        line-height: 42px;
    }

    &__btn {
        margin-top: 40px;
        padding: 25px 86px;
    }
}
