.contacts {
    margin-top: 130px;
    margin-bottom: -96px;

    &__items {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }

    &__item {
        background-color: $gray;
        color: $white;
        padding: 40px 65px 85px;

        &--addres {
            background-color: #5F5F5F;
        }

        &--phone {
            background-color: #6C6C6C;
        }

        &--email {
            background-color: #7B7B7B;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 24px;
        line-height: 28px;
    }

    &__content {
        margin-top: 10px;
        font-size: 16px;
        line-height: 29px;
        position: relative;

        &::before {
            content: '';
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            width: 20px;
            height: 20px;
        }

        &--addres {
            padding-left: calc(19px + 15px);
            margin-left: calc(-19px + -15px);

            &::before {
                background-image: url('../img/mark.svg');
                width: 19px;
                height: 27px;
            }
        }

        &--phone {
            padding-left: calc(23px + 15px);
            margin-left: calc(-23px + -15px);

            &::before {
                background-image: url('../img/phone--white.svg');
                width: 23px;
                height: 23px;
            }
        }

        &--email {
            padding-left: calc(24px + 15px);
            margin-left: calc(-24px + -15px);

            &::before {
                background-image: url('../img/at.svg');
                width: 24px;
                height: 24px;
            }
        }
    }
}
