@media (max-width: 1500px) {
    .hero {
        .swiper-button-prev {
            left: 30px !important;
        }

        .swiper-button-next {
            right: 30px !important;
        }
    }
}

@media (max-width: 1315px) {
    .about__container {
        display: block;
    }

    .why-us {
        margin-top: 50px;
    }

    .about__title {
        margin-bottom: 25px;
    }

    .services-security__container {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .services-security__left {
        margin-right: 0;
        max-width: 600px;
        width: 100%;
    }

    .services-security__right {
        margin-top: 50px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        max-width: 474px;
    }

    .services__items {
        width: 100%;
    }

    .services__title {
        text-align: center;
    }

    .services__subtitle {
        text-align: center;
    }

    .why-us__list {
        flex-direction: row;
    }
}

@media (max-width: 1050px) {
    .benefits__list {
        max-width: 350px;
        padding: 70px 25px;
    }

    .hero {
        .swiper-button-prev {
            left: 15px !important;
        }

        .swiper-button-next {
            right: 15px !important;
        }
    }

    .header__top {
        display: none;
        overflow: hidden;
    }

    .header__main {
        border-bottom: 2px solid $blue;
        position: fixed;
        background-color: darken(#F2F2F2, 3%);
        top: 0;
        left: 0;
        right: 0;
        z-index: 101;
    }

    .header-main__container {
        padding-top: 15px;
        padding-bottom: 15px;
    }

    .header-main__logo {
        width: 56px;
        height: 57px;
    }

    .contacts {
        margin-top: 110px;
        margin-bottom: -50px;
    }

    .contacts__item {
        padding-top: 15px;
        padding-bottom: 15px;
    }

    .footer-top{
        padding-top: 105px;
        padding-bottom: 60px;
    }

    .application__container {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }

    .application__right {
        margin: 0;
        margin-top: 50px;
        padding: 0;
    }

    .appointment-form__btn {
        width: 50%;
        align-self: center;
    }

    .application {
        margin-top: 100px;
    }

    .hero {
        margin-top: 89px;
    }

    .nav__list {
        flex-direction: column;
        position: absolute;
        top: 100%;
        z-index: 99;
        right: 0;
        border-left: 2px solid $blue;
        background-color: darken(#F2F2F2, 3%);
        width: 200px;
        height: 100vh;
        transform: translateX(calc(100% + 10px));
        transition: transform 0.3s ease-in-out;
    }

    .nav__list--active {
        transform: translateX(0);
    }

    .nav__item {}

    .nav__link {
        width: 100%;
        padding: 10px 10px;
        transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

        &:hover {
            background-color: $light-blue;
            color: $white;
        }
    }

    .burger {
        display: block;
        z-index: 999;
    }

    .header-main__container {
        justify-content: start;
    }

    .header-main__content {
        display: flex;
        justify-content: space-between;
        width: 100%;
    }

    .header-main__nav {
        order: 2;
    }

    .header-main__call {
        order: 1;
    }

    .nav__item:not(:last-child) {
        margin-right: 0px;
    }

    .burger {
        --burger-width: 35px;
        --burger-height: 20px;
        --burger-line-height: 2px;

        @include burger;
    }
}

@media (max-width: 900px) {
    .footer-bottom__copyright{
        text-align: center;
    }
    .footer-top__items{
        flex-wrap: wrap;
    }
    .footer-top__item{
        &--about{
            max-width: 100%;
            margin-bottom: 60px;
        }
    }
    .footer-top__title{
        margin-bottom: 15px;
    }
    .contacts__items {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }

    .contacts__item {
        text-align: center;
        padding-left: 20px;
        padding-right: 20px;

        &:not(:last-child) {
            margin-bottom: 5px;
        }
    }

    .contacts__title {
        font-size: 20px;
        line-height: 20px;
    }

    .contacts__content {
        font-size: 14px;
        line-height: 20px;
    }

    .services-security {
        margin-top: 70px;
    }

    .benefits__list {
        max-width: 100%;
    }

    .benefits {
        margin-top: 70px;
    }

    .benefits__title,
    .benefits__subtitle {
        text-align: center;
    }

    .benefits__btn {
        width: 100%;
    }

    .benefits__tabs {
        bottom: 0;
        top: auto;
        right: auto;
        width: 100%;
        transform: translateY(0);
    }

    .benefits__tab {
        text-align: center;
        width: 100%;
        padding: 8px 10px;
        border-top: 2px solid transparent;
        border-bottom: 2px solid transparent;

        &.active {
            border-top: 2px solid $blue;
            border-bottom: 2px solid $blue;
        }
    }

    .benefits__tab:not(:last-child) {
        margin-bottom: 10px;
    }

    .benefits__container {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .why-us__list {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .why-us__item {
        max-width: 100%;
    }

    .why-us__item {
        margin-top: 20px;
    }

    .hero-slide__container {
        justify-content: center;
    }

    .hero-slide__inner {
        text-align: center;
    }
}

@media (max-width: 700px) {
    .security__btn{
        width: 100%;
    }
    .application__title {
        font-size: 26px;
        line-height: 30px;
    }

    .application__subtitle {
        font-size: 20px;
        line-height: 22px;
    }

    .application__descr {
        font-size: 14px;
        line-height: 20px;
    }

    .application__link {
        color: #616161;
    }

    .appointment-form__btn {
        width: 100%;
    }

    .testimonials__title {
        font-size: 25px;
        line-height: 30px;
    }

    .testimonials__subtitle {
        font-size: 15px;
        line-height: 20px;
    }

    .testimonials__procent {
        font-size: 45px;
        line-height: 30px;
        text-align: right;
        margin-left: 5px;
    }

    .testimonials__procent p {
        margin-top: 10px;
        font-size: 15px;
        line-height: 20px;
    }

    .testimonials-block {
        margin-top: 40px;
        padding: 30px;
    }

    .testimonials-block__descr {
        font-size: 13px;
        line-height: 20px;
    }

    .testimonials-block__name {
        margin-top: 20px;
    }

    .testimonials__container {
        flex-direction: column;
        padding-top: 80px;
        padding-bottom: 70px;
    }

    .testimonials__right {
        margin-left: 0;
    }

    .testimonials__left {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .testimonials__procent {
        margin-top: 0;
    }

    .hero-slide__inner {
        max-width: 500px;
        padding-left: 40px;
        padding-right: 40px;
    }

    .hero-slide__subtitle {
        font-size: 20px;
        line-height: 28px;
    }

    .hero-slide__title {
        font-size: 55px;
        line-height: 68px;
    }

    .hero-slide__descr {
        font-size: 20px;
        line-height: 28px;
    }

    .hero-slide__btn {
        margin-top: 25px;
        padding: 18px 70px;
        font-size: 20px;
        line-height: 19px;
    }
}

@media (max-width: 450px) {

    .footer-top__items{
        flex-direction: column;
        justify-content: center;
    }
    .footer-top__item--social{
        margin-top: 60px;
    }

    .services__item-title {
        font-size: 20px;
        line-height: 24px;
    }

    .services-item-content__text {
        font-size: 14px;
        line-height: 22px;
    }

    .services-item-content__link {
        color: #4a4a4a;
    }

    .benefits__tab {
        font-size: 15px;
        line-height: 17px;
        border: 2px solid transparent;

        &.active {
            border: 2px solid $blue;
        }
    }

    .benefits__tabs {
        top: 110%;
    }

    .benefits__list {
        margin-top: 130px;
    }

    .hero .swiper-button-prev::after,
    .hero .swiper-button-next::after {
        background-size: 14px 28px;
        width: 14px;
        height: 28px;
        background-repeat: no-repeat;
    }

    .hero {
        .swiper-button-prev {
            left: 0px !important;
        }

        .swiper-button-next {
            right: 0px !important;
        }
    }

    .hero-slide__inner {
        padding-left: 25px;
        padding-right: 25px;
    }

    .hero-slide {
        background-position: top left -100px;
    }

    .hero-slide__title {
        font-size: 40px;
        line-height: 55px;
    }

    .container {
        padding-left: 10px;
        padding-right: 10px;
    }

    .header-main__call {
        margin-left: 10px;
        padding-left: 30px;
    }

    .header-main__call-title {
        font-size: 12px;
        line-height: 14px;
    }

    .header-main__call-phone {
        font-size: 13px;
        line-height: 15px;
    }
}
