.footer {
    &__top {
        background-color: #4A4A4A;
    }

    &__bottom {
        background-color: #363636;
    }
}

.footer-top {
    padding-top: 175px;
    padding-bottom: 125px;
    color: $white;

    &__items {
display: flex;
justify-content: space-between;
    }

    &__item {
        
        &--about {
            max-width: 340px;
        }

        &--links {
            max-width: 230px;
        }
    }

    &__title {
        font-size: 24px;
        line-height: 28px;
        font-weight: 700;
        margin-bottom: 45px;
    }

    &__descr {
        font-size: 16px;
        line-height: 29px;
    }

    &__element {
        &:not(:last-child) {
            margin-bottom: 5px;
        }
    }

    &__link {
        font-size: 16px;
        line-height: 29px;
        transition: color 0.3s ease-in-out;

        &:hover {
            color: darken($white, 10%);
        }
    }

    .social__link {
        width: 40px;
        height: 40px;
    }
}

.footer-bottom {
    &__copyright {
        font-size: 16px;
        line-height: 29px;
        color: $white;
        padding: 25px 0;
    }
}
