.header {
    &__top {
        background-color: $blue;
    }

    &__main {
        background-color: #F2F2F2;
        transition: transform 0.3s ease-in-out;
        width: 100%;

        &--position-fixed {
            position: fixed;
            transform: translateY(-110%);
            background-color: darken(#F2F2F2, 3%);
            box-shadow: 0 1px 0 0 $blue;
            top: 0px;
            left: 0;
            right: 0;
            z-index: 101;
            opacity: 0;

            .header-main__logo {
                width: 56px;
                height: 57px;
            }
        }

        &--opacity {
            opacity: 1;
        }

        &--fixed {
            transform: translateY(0);

            .header-main__container {
                padding-top: 15px;
                padding-bottom: 15px;
                transition: padding 0.3s ease-in-out;
            }
        }
    }
}

.burger {
    display: none;
}

.header-top {
    &__container {
        display: flex;
        justify-content: space-between;
        height: 40px;
        align-items: center;
    }

    &__social {}

    &__email {
        font-size: 12px;
        line-height: 14px;
        color: $white;
        transition: color 0.3s ease-in-out;
        position: relative;

        &::before {
            content: '';
            position: absolute;
            bottom: -1px;
            left: 0;
            background-color: darken($white, 7%);
            width: 0;
            height: 1px;
            transition: width 0.3s ease-in-out;
        }

        &:hover {
            color: darken($white, 7%);

            &::before {
                width: 100%;
            }
        }
    }
}

.header-main {
    &__container {
        display: flex;
        justify-content: space-between;
        padding-top: 20px;
        padding-bottom: 20px;
        align-items: center;
    }

    &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__nav {
        display: flex;
    }

    &__call {
        margin-left: 58px;
        padding-left: 34px;
        position: relative;

        &::before {
            content: '';
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            width: 23px;
            height: 33px;
            background-image: url('../img/phone.svg');
            background-repeat: no-repeat;
            background-size: cover;
        }
    }

    &__call-title {
        color: $gray;
        font-size: 14px;
        line-height: 16px;
    }

    &__call-phone {
        font-size: 16px;
        line-height: 18px;
        color: $black;
    }
}
