.about {
    margin-top: 85px;

    &__container {
        display: flex;
        justify-content: space-between;
    }

    &__title {
        margin: 0;
        margin-bottom: 55px;
        font-weight: 700;
        font-size: 32px;
        line-height: 37px;
        color: $black;
    }
}

.about-us {
    max-width: 770px;

    &__img {
        background-color: $blue;

        img {
            width: 100%;
            height: 100%;
            display: block;
            object-fit: cover;
        }
    }

    &__text {
        margin-top: 38px;
        font-size: 16px;
        line-height: 29px;
        color: $black;

        p:not(:last-child) {
            margin-bottom: 50px;
        }
    }
}

.why-us {
    &__content {
    
    }

    &__list {
        display: flex;
        flex-direction: column;
    }

    &__item {}
}

.why-us-item {
    position: relative;
    max-width: 350px;
    &::after {
        content: '';
        position: absolute;
        background-size: cover;
        background-repeat: no-repeat;
        width: 35px;
        height: 35px;
        top: 40px;
        right: 20px;
    }

    &--dependable {
        &::after {
            background-image: url('../img/why-us-1.svg');
            width: 33px;
            height: 35px;
        }
    }

    &--open {
        &::after {
            background-image: url('../img/why-us-2.svg');
            width: 27px;
            height: 27px;
        }
    }

    &--connected {
        &::after {
            background-image: url('../img/why-us-3.svg');
            width: 32px;
            height: 20px;
        }
    }

    &__wrapper {
        padding: 45px 30px 45px 37px;
        background-color: #F8F8F8;
        transition: background-color 0.3s ease-in-out;
       

        &:hover {
            background-color: #F3F3F3;

            .why-us-item__link {
                color: darken($gray, 10%);

                &::before {
                    width: 100%;
                }
            }
        }

    }

    &__title {
        color: $blue;
        font-weight: 700;
        font-size: 24px;
        line-height: 28px;
    }

    &__descr {
        margin-top: 17px;
        font-size: 16px;
        line-height: 25px;
        color: $black;
    }

    &__link {
        margin-top: 10px;
        text-transform: uppercase;
        font-size: 12px;
        line-height: 29px;
        font-weight: 700;
        color: $gray;
        transition: color 0.3s ease-in-out;
        position: relative;
        display: inline-block;

        &::before {
            content: "";
            position: absolute;
            bottom: 3px;
            left: 0;
            background-color: $blue;
            width: 0;
            height: 1px;
            transition: width 0.3s ease-in-out;
        }
    }
}
