.social {
    &__list {
        display: flex;
    }
    &__item:not(:last-child){
        margin-right: 21px;
    }
    &__link {
        width: 22px;
        height: 22px;
        display: block;
        svg {
            width: 100%;
            height: 100%;
            object-fit: cover;
            fill: $white;
            transition: fill 0.3s ease-in-out;
            &:hover{
                fill: darken($white, 7%);
            }
        }
    }
}
