.nav {
    &__list {
        display: flex;
    }

    &__item:not(:last-child) {
        margin-right: 20px;
    }

    &__link {
        font-size: 14px;
        line-height: 16px;
        font-weight: 400;
        color: $black;
        text-transform: uppercase;
        transition: color 0.3s ease-in-out;
        &:hover{
            color: lighten($black, 40%);
        }
        &--active {
            font-weight: 700;
            &:hover{
                color: currentColor;
            }
        }
    }
}
