.benefits {
    margin-top: 135px;
    &__container {
        display: flex;
    }

    &__left {
        position: relative;
        background-color: $blue;
        max-width: 650px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
        }
    }

    &__tabs {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
    }

    &__tab {
        display: block;
        background-color: rgba($blue, 0.9);
        mix-blend-mode: normal;
        color: $white;
        padding: 25px 35px;
        width: 100%;
        text-align: left;
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
        transition: background-color 0.3s ease-in-out;
        &:not(:last-child) {
            margin-bottom: 21px;
        }

        &.active {
            background-color: rgba($white, 0.9);
            color: $black;
            &:hover{
                background-color: rgba($white, 0.95);  
            }
        }
        &:hover{
            background-color: rgba($blue, 0.95);
        }
    }

    &__list {
        max-width: 520px;
        padding: 70px 25px;
    }

    &__item {
        display: none;
    }

    &__item.active {
        display: block;
    }

    &__title {
        font-weight: 700;
        font-size: 32px;
        line-height: 37px;
        color: $black;
    }

    &__subtitle {
        font-size: 24px;
        line-height: 28px;
        color: $gray;
        margin-top: 10px;
    }

    &__descr {
        margin-top: 38px;
    }

    &__descr-item {
        position: relative;
        padding-left: 12px;
        font-size: 16px;
        line-height: 29px;
        color: $black;

        &::before {
            content: '';
            position: absolute;
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: currentColor;
            left: 0;
            top: 13px;
        }

        &:not(:last-child) {
            margin-bottom: 10px;
        }
    }

    &__btn {
        margin-top: 30px;
        padding: 6px 22px;
        font-size: 12px;
        line-height: 23px;
    }
}
