.application {
    margin-top: 180px;

    &__container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    &__left {}

    &__item {
        &:not(:last-child) {
            margin-bottom: 30px;
        }
    }

    &__title {
        margin: 0;
        font-size: 32px;
        line-height: 37px;
        color: $black;
        font-weight: 700;
    }

    &__subtitle {
        margin: 0;
        font-size: 24px;
        line-height: 28px;
        margin-top: 10px;
        color: $gray;
    }

    &__descr {
        margin-top: 25px;
        color: #595959;
        font-size: 16px;
        line-height: 29px;
    }

    &__link {
        margin-top: 10px;
        font-weight: 700;
        font-size: 12px;
        line-height: 29px;
        color: $gray;
        padding: 5px 0;
        padding-right: 15px;
        transition: color 0.3s ease-in-out;
        text-transform: uppercase;

        &:hover {
            color: darken($gray, 11%);
        }
    }

    &__right {
        margin-top: -54px;
        margin-left: 120px;
        padding: 54px 40px 50px 45px;
        background: linear-gradient(180deg, #FCFCFC 0%, #F7F7F7 100%);
    }
}


.appointment-form {
    margin-top: 45px;
    display: flex;
    flex-direction: column;

    &__input {
        width: 100%;
        border: 2px solid #E2E2E2;
        background-color: $white;
        padding: 19px 25px 18px 15px;
        font-size: 14px;
        line-height: 16px;
        color: #4A4A4A;
        outline-color: $blue;
        &::placeholder {
            color: #4A4A4A;
        }

        &:not(:last-child) {
            margin-bottom: 35px;
        }
    }

    &__btn {
        padding: 6px 37px;
        font-size: 12px;
        line-height: 23px;
        align-self: flex-end;
    }
}
