.testimonials {
    margin-top: 150px;
    background-color: $blue;

    &__container {
        padding-top: 150px;
        padding-bottom: 150px;
        display: flex;
        justify-content: space-between;
    }

    &__left {
        color: $white;
    }

    &__title {
        font-weight: 700;
        font-size: 32px;
        line-height: 37px;
    }

    &__subtitle {
        font-weight: 400;
        margin: 0;
        font-size: 24px;
        line-height: 28px;
        margin-top: 10px;
    }

    &__procent {
        margin-top: 75px;
        font-size: 64px;
        line-height: 73px;
        font-weight: 700;

        p {
            margin-top: 1px;
            font-size: 28px;
            line-height: 32px;
            font-weight: 400;
        }
    }

    &__right {
        max-width: 734px;
        margin-left: 50px;
    }
}

.testimonials-block {
    padding: 75px 50px;
    color: $black;
    background-color: $white;

    &__title {
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
    }

    &__descr {
        margin-top: 15px;
        font-size: 16px;
        line-height: 29px;
    }

    &__name {
        font-size: 16px;
        line-height: 18px;
        font-weight: 700;
        margin-top: 50px;
    }
}
